import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the KotaDetailsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-kota-details',
  templateUrl: 'kota-details.html',
})
export class KotaDetailsPage {
  kotaDetails : any;
  constructor(public navCtrl: NavController, public navParams: NavParams) {
  this.kotaDetails = navParams.get("kategori");  
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad KotaDetailsPage');
  }

}



